'use client'

import React, { useState } from 'react';
import Link from 'next/link';
import { ArrowUpRight, Thermometer, Droplets, Menu, X, ChevronUp, MapPin } from 'lucide-react';
import {
  getAirQualityColor,
  formatPMValue,
  getRecommendationIcon,
  useMonitoringData,
  useLocationMonitoringData,
  getGeneralRecommendations,
  getSensitiveRecommendations,
  determineAirQuality // เพิ่ม import นี้
} from '../data/monitoring-data';

// Helper function เพื่อกำหนดระดับความเสี่ยงสำหรับกลุ่มต่างๆ
const getAirQualityForGroup = (pc01, pm01, pm25, pm100, groupType = 'general') => {
  // เรียกใช้ determineAirQuality โดยตรงจาก monitoring-data.js
  return determineAirQuality(pc01, pm01, pm25, pm100);
};

const Header = () => {
  const [isMenuOpen, setIsMenuOpen] = useState(false);

  return (
    <header className="h-18 sm:h-20 bg-green-100 border-b border-green-100 flex-shrink-0">
      <div className="h-full flex items-center justify-between px-4 sm:px-6">
        <div className="flex items-center gap-4 sm:gap-6">
          <div className="w-16 h-16 sm:w-18 sm:h-18 flex-shrink-0 flex items-center justify-center">
            <img
              src="/assets/images/logo.png"
              alt="Mupcop logo"
              className="w-14 h-14 sm:w-16 sm:h-16 object-contain"
              width={64}
              height={64}
            />
          </div>
          <div>
            <h1 className="text-xl sm:text-2xl lg:text-3xl xl:text-4xl font-bold text-black font-montserrat">Mupcop</h1>
            <h2 className="text-base sm:text-lg lg:text-xl font-light text-black max-w-xl font-montserrat">
              Multi-factor ultrafine particle optimization prediction
            </h2>
          </div>
        </div>

        <nav className="hidden sm:flex items-center gap-4 sm:gap-6">
          <Link href="/" className="px-4 py-2 text-lg sm:text-xl lg:text-2xl font-semibold text-black rounded-lg hover:bg-green-200 transition-colors font-montserrat">
            Air quality
          </Link>
          <Link href="/history-data" className="px-4 py-2 text-lg sm:text-xl lg:text-2xl font-semibold text-black rounded-lg hover:bg-green-200 transition-colors font-montserrat">
            History data
          </Link>
        </nav>

        <button
          className="sm:hidden"
          onClick={() => setIsMenuOpen(!isMenuOpen)}
        >
          {isMenuOpen ? <X size={24} /> : <Menu size={24} />}
        </button>
      </div>
    </header>
  );
};

const MonitoringPanel = ({ selectedLocation, onLocationClear }) => {
  // State สำหรับ popup
  const [showRecommendationPopup, setShowRecommendationPopup] = useState(false);
  const [selectedRecommendations, setSelectedRecommendations] = useState([]);
  const [selectedGroupType, setSelectedGroupType] = useState('general');

  // Debug การรับข้อมูล
  console.log('MonitoringPanel received selectedLocation:', selectedLocation);

  // ใช้ข้อมูลตามตำแหน่งที่เลือก หรือใช้ข้อมูลเริ่มต้น
  const defaultData = useMonitoringData();
  const locationData = useLocationMonitoringData(selectedLocation);

  console.log('Default data status:', !!defaultData);
  console.log('Location data status:', !!locationData);
  console.log('Selected location for monitoring:', selectedLocation);

  // เลือกข้อมูลที่จะแสดง
  const data = selectedLocation ? locationData : defaultData;
  console.log('Final data being used:', !!data, 'from', selectedLocation ? 'location' : 'default');

  const statusColor = getAirQualityColor(data.mainReading.status);

  // คำนวณระดับสำหรับแต่ละกลุ่ม - ใช้ determineAirQuality
  const sensitiveStatus = data.pmReadings ? getAirQualityForGroup(
    parseFloat(data.pmReadings.find(r => r.type === 'PM0.1')?.value || 0),
    parseFloat(data.pmReadings.find(r => r.type === 'PM2.5')?.value || 0),
    parseFloat(data.pmReadings.find(r => r.type === 'PM10')?.value || 0),
    'sensitive'
  ) : 'Good';

  const generalStatus = data.pmReadings ? getAirQualityForGroup(
    parseFloat(data.pmReadings.find(r => r.type === 'PM0.1')?.value || 0),
    parseFloat(data.pmReadings.find(r => r.type === 'PM2.5')?.value || 0),
    parseFloat(data.pmReadings.find(r => r.type === 'PM10')?.value || 0),
    'general'
  ) : 'Good';

  // ฟังก์ชันสำหรับเปิด popup
  const handleRecommendationClick = (groupType) => {
    const recommendations = groupType === 'sensitive'
      ? getSensitiveRecommendations(sensitiveStatus)
      : getGeneralRecommendations(generalStatus);

    setSelectedRecommendations(recommendations);
    setSelectedGroupType(groupType);
    setShowRecommendationPopup(true);
  };

  // ฟังก์ชันสำหรับปิด popup
  const closePopup = () => {
    setShowRecommendationPopup(false);
    setSelectedRecommendations([]);
  };

  if (!data) return (
    <div className="flex items-center justify-center h-full">
      <div className="text-center">
        <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-gray-800 mb-3"></div>
        <p className="text-lg sm:text-xl text-gray-600 font-sarabun">กำลังโหลดข้อมูล...</p>
      </div>
    </div>
  );

  return (
    <div className="w-full lg:w-1/3 bg-gray-50/50 border-r border-gray-100 overflow-auto relative">
      <div className="p-3 sm:p-4">
        {/* Location Header - เน้นตัวอักษร */}
        <div className="flex items-center justify-between mb-3 sm:mb-4">
          <h2 className="text-xl sm:text-2xl lg:text-3xl xl:text-4xl font-light text-black-800 font-montserrat">UFPs Monitoring</h2>
          {selectedLocation && onLocationClear && (
            <button
              onClick={onLocationClear}
              className="text-base sm:text-lg text-gray-500 hover:text-gray-700 bg-gray-100 hover:bg-gray-200 px-3 py-2 rounded-md transition-colors font-sarabun"
            >
              × ล้าง
            </button>
          )}
        </div>

        {/* Main reading card - เน้นตัวอักษร */}
        <div className="bg-white rounded-lg p-3 sm:p-4 shadow-sm border border-gray-100 mb-3 sm:mb-4">
          <div className="flex items-center justify-between mb-3">
            <div className="flex items-center gap-3">
              <div className="w-3 h-3 bg-green-500 rounded-full animate-pulse" />
              <span className="text-lg sm:text-xl lg:text-2xl xl:text-3xl font-semibold text-black font-montserrat">LIVE</span>
            </div>
            <span className="text-base sm:text-lg lg:text-xl text-black font-numbers">{data.date}</span>
          </div>

          {/* Location name in main reading - เน้นตัวอักษร */}
          <div className="text-base sm:text-lg lg:text-xl text-gray-600 mb-3 sm:mb-4">
            <div className="flex flex-col sm:flex-row sm:items-center sm:justify-between gap-2 sm:gap-0">
              <div className="flex items-center gap-2">
                <MapPin className="w-4 h-4 sm:w-5 sm:h-5" />
                <span className="truncate text-base sm:text-lg lg:text-xl font-sarabun">
                  {selectedLocation ? selectedLocation.name : "Cafe Amazon สาขา ST"}
                </span>
              </div>
              <div className="text-base sm:text-lg lg:text-xl text-gray-500 sm:ml-2">
                <span className="font-sarabun">อัพเดท: </span>
                <span className="font-numbers">{data.time}</span>
              </div>
            </div>
          </div>

          {/* PM Reading Section - ใช้ไอคอน CSS แทนรูปภาพ */}
          <div className="flex rounded-lg mb-3 sm:mb-4 shadow-sm overflow-hidden min-h-[140px] sm:min-h-[160px]">
            {/* ส่วนซ้าย: ค่า PM และหน่วย */}
            <div
              className="w-[50%] sm:w-[45%] p-2 sm:p-3 flex flex-col justify-center relative"
              style={{ backgroundColor: statusColor }}
            >
              <div className="absolute inset-0 bg-black bg-opacity-30"></div>
              <div className="relative z-10 text-center">
                <div className="text-sm sm:text-base lg:text-lg xl:text-xl font-bold mb-2 text-white opacity-90 font-montserrat">
                  {data.mainReading.type || 'PM0.1'}
                </div>
                <div className="text-3xl sm:text-4xl lg:text-5xl xl:text-6xl font-semibold text-white mb-2 leading-tight break-all font-numbers" style={{ textShadow: '3px 3px 6px rgba(0,0,0,0.8)' }}>
                  {data.mainReading.type === 'PM0.1'
                    ? Number(data.mainReading.value).toFixed(3)
                    : data.mainReading.value}
                </div>
                <div className="text-sm sm:text-base lg:text-lg xl:text-xl font-semibold text-white opacity-90 font-montserrat" >
                  {data.mainReading.unit}
                </div>
              </div>
            </div>

            {/* ส่วนขวา: แบ่งเป็น 2 ส่วน บน-ล่าง - ใช้ไอคอน CSS */}
            <div
              className="w-[50%] sm:w-[55%] flex flex-col"
              style={{ backgroundColor: statusColor }}
            >
              {/* ส่วนบน: กลุ่มเปราะบาง */}
              <button
                onClick={() => handleRecommendationClick('sensitive')}
                className="flex-1 flex items-center gap-2 sm:gap-3 p-2 sm:p-3 transition-all hover:bg-black hover:bg-opacity-10 border-b border-white/20"
              >
                <div className="flex-shrink-0 ml-2 sm:ml-4"> {/* เพิ่ม ml-2 sm:ml-4 เพื่อขยับไอคอนไปทางขวา */}
                  <i className="bi bi-person-exclamation text-white text-3xl sm:text-4xl lg:text-5xl drop-shadow-lg" ></i>
                </div>
                <div className="flex-1 text-center min-w-0">
                  <div className="text-xs sm:text-sm lg:text-xl font-semibold text-white opacity-90 mb-1 font-sarabun">
                    กลุ่มเปราะบาง
                  </div>
                  <div className="text-sm sm:text-base lg:text-lg xl:text-3xl font-bold text-white truncate font-montserrat" style={{ textShadow: '2px 2px 4px rgba(0, 0, 0, 0.8)' }}>
                    {sensitiveStatus}
                  </div>
                </div>
                <div className="flex-shrink-0 text-white text-sm sm:text-base opacity-60">
                  →
                </div>
              </button>

              {/* ส่วนล่าง: คนทั่วไป */}
              <button
                onClick={() => handleRecommendationClick('general')}
                className="flex-1 flex items-center gap-2 sm:gap-3 p-2 sm:p-3 transition-all hover:bg-black hover:bg-opacity-10"
              >
                <div className="flex-shrink-0 ml-2 sm:ml-4"> {/* เพิ่ม ml-2 sm:ml-4 เพื่อขยับไอคอนไปทางขวา */}
                  <i className="bi bi-person-check text-white text-3xl sm:text-4xl lg:text-5xl drop-shadow-lg" ></i>
                </div>
                <div className="flex-1 text-center min-w-0">
                  <div className="text-xs sm:text-sm lg:text-xl font-semibold text-white opacity-90 mb-1 font-sarabun">
                    กลุ่มคนทั่วไป
                  </div>
                  <div className="text-sm sm:text-base lg:text-2lg xl:text-3xl font-bold text-white truncate font-montserrat" style={{ textShadow: '2px 2px 4px rgba(0,0,0,0.8)' }}>
                    {generalStatus}
                  </div>
                </div>
                <div className="flex-shrink-0 text-white text-sm sm:text-base opacity-60">
                  →
                </div>
              </button>
            </div>
          </div>

          {/* Temperature and Humidity - เน้นตัวอักษร */}
          <div className="flex justify-center gap-8 sm:gap-12 text-base sm:text-lg lg:text-xl mb-3 sm:mb-4 py-3 sm:py-4 bg-gray-50 rounded-lg">
            {/* Temperature */}
            <div className="flex items-center gap-3 sm:gap-4">
              <img
                src="/assets/images/temperature.png"
                alt="Temperature"
                className="w-8 h-8 sm:w-10 sm:h-10"
                onError={(e) => {
                  e.target.style.display = 'none';
                  e.target.nextSibling.style.display = 'inline';
                }}
              />
              <Thermometer className="w-5 h-5 sm:w-6 sm:h-6 text-gray-500" style={{ display: 'none' }} />
              <span className="text-gray-700 font-medium text-base sm:text-lg lg:text-xl xl:text-2xl font-numbers">{data.conditions.temperature}</span>
            </div>

            {/* Humidity */}
            <div className="flex items-center gap-3 sm:gap-4">
              <img
                src="/assets/images/humidity.png"
                alt="Humidity"
                className="w-8 h-8 sm:w-10 sm:h-10"
                onError={(e) => {
                  e.target.style.display = 'none';
                  e.target.nextSibling.style.display = 'inline';
                }}
              />
              <Droplets className="w-5 h-5 sm:w-6 sm:h-6 text-gray-500" style={{ display: 'none' }} />
              <span className="text-gray-700 font-medium text-base sm:text-lg lg:text-xl xl:text-2xl font-numbers">{data.conditions.humidity}</span>
            </div>
          </div>

          {/* Recommendations Icons - ลดขนาดมากขึ้น */}
          <div className="border-t border-gray-100 pt-2 sm:pt-3">
            <div className="text-center">
              <div className="flex justify-center items-center gap-3 sm:gap-4 p-1 flex-wrap">
                {data.recommendations.slice(0, 5).map((recommendation, index) => {
                  const iconPath = getRecommendationIcon(recommendation);
                  const isImageIcon = typeof iconPath === 'string' && iconPath.startsWith('/assets/images/');

                  return (
                    <div key={index} className="flex flex-col items-center">
                      {isImageIcon ? (
                        <img
                          src={iconPath}
                          alt=""
                          className="w-4 h-4 sm:w-6 sm:h-6 lg:w-7 lg:h-7"
                          onError={(e) => {
                            e.target.style.display = 'none';
                            e.target.nextSibling.style.display = 'inline';
                          }}
                        />
                      ) : (
                        <span className="text-sm sm:text-base lg:text-lg">{iconPath}</span>
                      )}
                      {isImageIcon && (
                        <span style={{ display: 'none' }} className="text-sm sm:text-base lg:text-lg">•</span>
                      )}
                    </div>
                  );
                })}
              </div>
              <div className="text-xs sm:text-sm lg:text-base text-gray-500 mt-1 font-sarabun">
                คลิกที่ bubble สีด้านบนเพื่อดูคำแนะนำสำหรับแต่ละกลุ่ม
              </div>
            </div>
          </div>
        </div>

        {/* PM Summary Card - เน้นตัวอักษร */}
        <div className="bg-white rounded-lg p-2 sm:p-3 shadow-sm border border-gray-100 mb-2 sm:mb-3">
          <h3 className="text-base sm:text-lg lg:text-xl xl:text-2xl font-medium text-gray-800 mb-2 sm:mb-3 font-sarabun">ข้อมูลมลพิษทางอากาศ</h3>
          <div className="grid grid-cols-2 gap-1 sm:gap-2">
            {data.pmReadings && data.pmReadings.length > 0 ? (
              data.pmReadings
                .filter(reading => reading.type === 'PM2.5' || reading.type === 'PM10')
                .map((reading, index) => {
                  // ตรวจสอบว่า reading มีข้อมูลครบ
                  if (!reading || !reading.type || reading.value === undefined) {
                    return null;
                  }

                  // แยกค่าตัวเลขจาก string หรือรับตัวเลขตรง ๆ
                  let numericValue;
                  if (typeof reading.value === 'number') {
                    numericValue = reading.value;
                  } else {
                    numericValue = parseFloat(reading.value.toString().replace(/[^\d.-]/g, ''));
                  }

                  // ตรวจสอบว่าค่าที่ได้เป็น number ที่ถูกต้อง
                  if (isNaN(numericValue)) {
                    return null;
                  }

                  // กำหนดสถานะโดยใช้ determineAirQuality
                  const status = determineAirQuality(
                    parseFloat(data.pmReadings.find(r => r.type === 'PM0.1')?.value || 0),
                    reading.type === 'PM2.5' ? numericValue : parseFloat(data.pmReadings.find(r => r.type === 'PM2.5')?.value || 0),
                    reading.type === 'PM10' ? numericValue : parseFloat(data.pmReadings.find(r => r.type === 'PM10')?.value || 0)
                  );

                  const bgColor = getAirQualityColor(status);

                  return (
                    <div
                      key={`${reading.type}-${selectedLocation?.id || 'default'}-${index}`}
                      className="rounded-lg p-1 sm:p-2 text-white text-center"
                      style={{ backgroundColor: bgColor }}
                    >
                      <div className="text-xl sm:text-xl font-medium mb-1 opacity-90 font-montserrat">{reading.type}</div>
                      <div className="text-base sm:text-3xl font-bold font-numbers" style={{ textShadow: '2px 2px 4px rgba(0,0,0,0.8)' }}>{numericValue.toFixed(3)}</div>
                      <div className="text-xl sm:text-xl opacity-90 font-montserrat">μg/m³</div>
                    </div>
                  );
                })
            ) : (
              <div className="col-span-2 text-center text-xs sm:text-sm text-gray-500 py-4 font-sarabun">
                ไม่มีข้อมููลมลพิษในขณะนี้
              </div>
            )}
          </div>
        </div>

        {/* Click instruction - เน้นตัวอักษร */}
        <div className="mt-3 p-3 bg-yellow-50 border border-yellow-200 rounded-lg">
          <div className="text-base sm:text-lg lg:text-xl text-yellow-800 font-sarabun">
            💡 <strong>วิธีใช้:</strong> คลิกที่หมุดบนแผนที่เพื่อดูข้อมูลคุณภาพอากาศของตำแหน่งนั้น
          </div>
        </div>
      </div>

      {/* Popup Modal - เน้นตัวอักษร */}
      {showRecommendationPopup && (
        <div
          className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50"
          onClick={closePopup}
        >
          <div
            className="bg-white rounded-lg max-w-xl w-full mx-4 max-h-[75vh] overflow-y-auto"
            onClick={(e) => e.stopPropagation()}
          >
            {/* Popup Header */}
            <div className="flex items-center justify-between p-4 sm:p-6 border-b">
              <h3 className="text-xl sm:text-2xl lg:text-3xl font-semibold text-gray-800 font-sarabun">
                คำแนะนำสำหรับ{selectedGroupType === 'sensitive' ? 'กลุ่มเปราะบาง' : 'คนทั่วไป'}
              </h3>
              <button
                onClick={closePopup}
                className="text-gray-500 hover:text-gray-700 text-2xl sm:text-3xl font-bold"
              >
                ×
              </button>
            </div>

            {/* Popup Content */}
            <div className="p-4 sm:p-6">
              <div className="mb-4 text-base sm:text-lg lg:text-xl text-gray-600 font-sarabun">
                📍 {selectedLocation ? selectedLocation.name : "Cafe Amazon สาขา ST"}
              </div>
              <div className="space-y-4">
                {selectedRecommendations.map((recommendation, index) => {
                  const iconPath = getRecommendationIcon(recommendation);
                  const isImageIcon = typeof iconPath === 'string' && iconPath.startsWith('/assets/images/');

                  return (
                    <div
                      key={`popup-rec-${index}`}
                      className="flex items-center gap-4 p-3 sm:p-4 bg-gray-50 rounded-lg"
                    >
                      <span className="flex-shrink-0">
                        {isImageIcon ? (
                          <img
                            src={iconPath}
                            alt=""
                            className="w-6 h-6 sm:w-8 sm:h-8"
                            onError={(e) => {
                              e.target.style.display = 'none';
                              e.target.nextSibling.style.display = 'inline';
                            }}
                          />
                        ) : (
                          <span className="text-lg sm:text-xl">{iconPath}</span>
                        )}
                        {isImageIcon && (
                          <span style={{ display: 'none' }} className="text-lg sm:text-xl">•</span>
                        )}
                      </span>
                      <span className="flex-1 text-sm sm:text-base lg:text-lg text-gray-700 leading-relaxed font-sarabun">
                        {recommendation}
                      </span>
                    </div>
                  );
                })}
              </div>
            </div>

            {/* Popup Footer */}
            <div className="px-4 sm:px-6 pb-4 sm:pb-6">
              <button
                onClick={closePopup}
                className="w-full bg-green-500 hover:bg-green-600 text-white py-3 px-4 rounded-lg transition-colors text-lg sm:text-xl lg:text-2xl font-medium font-sarabun"
              >
                ปิด
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

const Footer = () => (
  <footer className="h-18 sm:h-20 bg-green-100 border-t border-green-100 flex-shrink-0 flex items-center justify-between p-4 sm:p-6">
    <span className="text-base sm:text-lg lg:text-xl xl:text-2xl text-black font-montserrat">Made with by Jaejae Dream Yok ❤️</span>
    <div className="flex items-center gap-4 sm:gap-6">
      <div className="flex-shrink-0 flex items-center justify-center">
        <img
          src="/assets/images/logo.png"
          alt="Mupcop logo"
          className="w-14 h-14 sm:w-16 sm:h-16 object-contain"
          width={64}
          height={64}
        />
      </div>
      <div>
        <p className="text-xl sm:text-2xl lg:text-3xl xl:text-4xl font-bold text-black font-montserrat">Mupcop</p>
      </div>
    </div>
  </footer>
);

export { Header, MonitoringPanel, Footer };